"use client"

import { useState } from "react"
import { useParams } from "next/navigation"
import Link from "next/link"
import { ArrowLeft, PlayCircle } from "lucide-react"

import Header from "@/components/header"
import { Button } from "@/components/ui/button"
import { courseDetailsMap } from "@/lib/courses-data"

export default function PlaylistPage() {
    const [activeLessonIndex, setActiveLessonIndex] = useState(0)
    const params = useParams()
    const courseId = params.id as string
    const course = courseDetailsMap[courseId]

    if (!course) {
        return (
            <div className="min-h-screen bg-background">
                <Header />
                <main className="max-w-7xl mx-auto px-4 md:px-6 py-12">
                    <div className="text-center space-y-4">
                        <p className="text-lg text-muted-foreground">Course not found</p>
                        <Link href="/">
                            <Button variant="outline">
                                <ArrowLeft className="w-4 h-4 mr-2" />
                                Back to courses
                            </Button>
                        </Link>
                    </div>
                </main>
            </div>
        )
    }

    const playlistId = course.youtubePlaylistId || (course.url.includes("list=") ? course.url.split("list=")[1].split("&")[0] : "")

    return (
        <div className="min-h-screen bg-background flex flex-col">
            <Header />
            <main className="flex-1 max-w-[1600px] mx-auto w-full p-4 md:p-6">
                <div className="mb-4 flex items-center gap-4">
                    <Link href={`/course/${courseId}`}>
                        <Button variant="ghost" size="sm">
                            <ArrowLeft className="w-4 h-4 mr-2" />
                            Back to Course
                        </Button>
                    </Link>
                    <h1 className="text-xl font-bold truncate">{course.title}</h1>
                </div>

                <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                    {/* Main Video Player Area */}
                    <div className="lg:col-span-2 space-y-4">
                        <div className="aspect-video w-full rounded-xl overflow-hidden bg-black shadow-lg">
                            <iframe
                                key={activeLessonIndex}
                                width="100%"
                                height="100%"
                                src={`https://www.youtube.com/embed/videoseries?list=${playlistId}&index=${activeLessonIndex}`}
                                title={`${course.title} - Video ${activeLessonIndex + 1}`}
                                className="w-full h-full"
                                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                                allowFullScreen
                            />
                        </div>
                        <div>
                            <h2 className="text-2xl font-bold mb-2">
                                {course.lessons[activeLessonIndex]?.title || `Lesson ${activeLessonIndex + 1}`}
                            </h2>
                            <div className="flex items-center gap-4 text-sm text-muted-foreground">
                                <span>{course.instructor}</span>
                                <span>‚Ä¢</span>
                                <span>{course.lessons[activeLessonIndex]?.duration} mins</span>
                            </div>
                        </div>
                    </div>

                    {/* Playlist Sidebar */}
                    <div className="lg:col-span-1">
                        <div className="border rounded-xl overflow-hidden bg-card flex flex-col h-[calc(100vh-200px)] lg:h-[600px]">
                            <div className="p-4 border-b bg-muted/30">
                                <h3 className="font-semibold text-lg">Course Content</h3>
                                <p className="text-sm text-muted-foreground">
                                    {activeLessonIndex + 1} / {course.lessons.length} lessons
                                </p>
                            </div>
                            <div className="flex-1 overflow-y-auto">
                                <div className="p-2 space-y-1">
                                    {course.lessons.map((lesson, index) => (
                                        <button
                                            key={lesson.id}
                                            onClick={() => setActiveLessonIndex(index)}
                                            className={`w-full text-left p-3 rounded-lg text-sm transition-colors flex gap-3 group ${index === activeLessonIndex
                                                ? "bg-primary/10 hover:bg-primary/15"
                                                : "hover:bg-muted"
                                                }`}
                                        >
                                            <div className="flex-shrink-0 mt-0.5">
                                                {index === activeLessonIndex ? (
                                                    <PlayCircle className="w-5 h-5 text-primary" />
                                                ) : (
                                                    <div className="w-5 h-5 rounded-full border-2 border-muted-foreground/30 flex items-center justify-center text-[10px] text-muted-foreground">
                                                        {index + 1}
                                                    </div>
                                                )}
                                            </div>
                                            <div className="flex-1 min-w-0">
                                                <p className={`font-medium truncate ${index === activeLessonIndex ? "text-primary" : "text-foreground"
                                                    }`}>
                                                    {lesson.title}
                                                </p>
                                                <p className="text-xs text-muted-foreground mt-1">
                                                    {lesson.duration} mins
                                                </p>
                                            </div>
                                        </button>
                                    ))}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </div>
    )
}
